<template>
  <div class="arertInput" :class="showConsumption?'active':''"> 
    <div class="test">请确认用户是否已消费</div>
    <div class="btn">
        <div class="colse" @click="$emit('cance')">取消</div>
        <div class="submit" @click="submit">确定</div>
    </div>
  </div>
</template>
<script>
import { verification } from '@/api/order.js'
export default {
  props:{
    showConsumption:{
      default:false
    },
    verify_code:{
      default:''
    }
  },
  data() {
    return {
      // verify_code:'', // 核销码
    }
  },
  methods: {
    submit() {
      verification({
        verify_code:this.verify_code
      }).then(data => {
        // if(data.code === 0) {
          this.$emit('submit',1)
        // }
      })
    }
  }  
}
</script>
<style lang="less" scoped>
  .arertInput{
    width: 6rem;
    height: 2.84rem;
    position: fixed;
    background-color: #fff;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%) scale(0);
    overflow: hidden;
    border-radius: .1rem;
    transition: all .5s;
    // padding: .6rem .5rem .4rem;
    .test{
        width: 100%;
        height: 2.04rem;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: MicrosoftYaHei;
        font-size: .32rem;
        font-weight: normal;
        font-stretch: normal;
        line-height: .44rem;
        letter-spacing: 0rem;
        color: #000000;
    }
  }
  .btn{
      width: 100%;
      height: .8rem;
      display: flex;
      div{
        flex:1;
        font-family: MicrosoftYaHei;
        font-size: .34rem;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0px;
        color: #333333;
        background-color: #ccc;
        text-align: center;
        line-height: .8rem;
      }
      .submit{
          background-color: #4a57ff;
          color:#fff;
      }
  }
  .arertInput.active{
    transform: translate(-50%,-50%) scale(1);
    transition: all .5s;
  }
</style>



