<template>
  <div id="order" ref="order">
    <mt-header title="订单管理" style="height:.88rem;background-color:#fafafa;color:#000;">
        <mt-button icon="back" slot="left" @click="goback"></mt-button>
    </mt-header>
    <div class="table_nav">
        <div :class="verify_status==1?'active':''" @click="changeState(1)">未核销</div>
        <div :class="verify_status!=1?'active':''" @click="changeState(2)">已完成</div>
    </div>
    <!-- <scroll 
        :probeType="3"
        :listenScroll='true'
        :data='list'
        @pulldown="loadMore"
    > -->
    <div class="content" >
        <div class='loading' v-if="busy1"><img :src="ImgUrl+'home/loading.gif'" alt=""></div>
        <div class="item_order" v-for="(item,index) in list" :key="index" >
            <div class="hander_order">
                <div class="hander_top">
                    <div class="hander_time">
                        <div class="tuan" v-if="item.type==1">团</div>
                        <div class="dan" v-if="item.type==3&&verify_status!=1">单</div>
                        <div class="mai" v-if="item.type==2">买</div>
                        <div class="time">下单时间：{{item.created_at}}</div>
                    </div>
                    <div class="order_state">
                        <span v-if="item.status==7">已完成</span>
                        <span v-if="item.status==6">已退款</span>
                        <span v-if="item.status!=7&&item.status!=6" class="hexiao" @click="changeShp">核销</span>
                    </div>
                </div>
            </div>
            <div class="price" :class="item.dishes_items.length==0?'active1':''">
                <div class="user_price" >
                    <div class="user_price_left">
                        <img :src="item.user.avatar_url" alt="">
                        <div>{{item.user.name}}</div>
                    </div>
                    <div class="user_price_right">
                        实付：￥{{item.pay_price}}
                    </div>
                    
                </div>
                <ul class="meal_order" v-if="item.type==1">
                    <li>
                        <div class="meal_name">
                            <span>{{item.goods_name}}</span>
                            <span>￥{{item.pay_price}}(×{{item.buy_number}})</span>
                        </div>
                    </li>
                </ul>
                <!-- <div class="see_more" v-if='item.type!=2'>
                    <div class="more">
                        <span v-if="activeIndex===0" @click="changeMoreShow(-1)">收起更多</span>
                        <span v-if="activeIndex!=0" @click="changeMoreShow(0)">查看更多 </span>
                        <img :class="activeIndex===0?'active':''" src="/static/home/more.png">
                    </div>
                </div> -->
                <ul class="product_order" v-if="item.type==3&&verify_status!=1">
                            <li v-for="(obj,i) in item.dishes_items" :key="i" v-if="i==0||activeIndex==index">
                                <span>{{obj.dishes_goods_name}}</span>
                                <span>￥{{obj.dishes_goods_sale_price}}(×{{obj.number}})</span>
                            </li>
                        </ul>
                <div class="see_more" v-if="item.dishes_items.length>1">
                    <div class="more">
                        <span v-if="activeIndex==index" @click="changeMoreShow(-1)">收起更多</span>
                        <span v-if="activeIndex!=index" @click="changeMoreShow(index)">查看更多 </span>
                        <img :class="activeIndex==index?'active':''" :src="ImgUrl+'home/more.png'">
                    </div>
                </div>
            </div>
            <div class="order_num">
                <span>订单号：{{item.order_no}}</span>
                <img :src="ImgUrl+'home/phone.png'" @click="telPhone(item.notify_mobile)">
            </div>
        </div>
        <!-- <div class="item_order" v-for="(item,index) in list" :key="index" v-if="item.type==3&&verify_status!=1">
            <div class="hander_order">
                <div class="hander_top">
                    <div class="hander_time">
                        <div class="dan" >单</div>
                        <div class="time">下单时间：{{item.created_at}}</div>
                    </div>
                    <div class="order_state">
                        <span v-if="item.status==7">已完成</span>
                        <span v-if="item.status==6">已退款</span>
                        <span v-if="item.status!=7&&item.status!=6" class="hexiao" @click="changeShp">核销</span>
                    </div>
                </div>
            </div>
            <div class="price">
                <div class="user_price">
                    <div class="user_price_left">
                        <img :src="item.user.avatar_url" alt="">
                        <div>{{item.user.name}}</div>
                    </div>
                    <div class="user_price_right">
                        实付：￥{{item.pay_price}}
                    </div>
                </div>
                        <ul class="product_order">
                            <li v-for="(obj,i) in item.dishes_items" :key="i" v-if="i==0||activeIndex==index">
                                <span>{{obj.dishes_goods_name}}</span>
                                <span>￥{{obj.dishes_goods_sale_price}}(×{{obj.number}})</span>
                            </li>
                        </ul>
                <div class="see_more" v-if="item.dishes_items.length>1">
                    <div class="more">
                        <span v-if="activeIndex==index" @click="changeMoreShow(-1)">收起更多</span>
                        <span v-if="activeIndex!=index" @click="changeMoreShow(index)">查看更多 </span>
                        <img :class="activeIndex==index?'active':''" :src="ImgUrl+'home/more.png'">
                    </div>
                </div>
            </div>
            <div class="order_num">
                <span>订单号：{{item.order_no}}</span>
                <img :src="ImgUrl+'home/phone.png'" @click="telPhone(item.notify_mobile)">
            </div>
        </div>
        <div class="item_order" v-for="(item,index) in list" :key="index" v-if="item.type==2">
            <div class="hander_order">
                <div class="hander_top">
                    <div class="hander_time">
                        <div class="mai">买</div>
                        <div class="time">下单时间：{{item.created_at}}</div>
                    </div>
                    <div class="order_state">
                        <span v-if="item.status==7">已完成</span>
                        <span v-if="item.status==6">已退款</span>
                        <span v-if="item.status!=7&&item.status!=6" class="hexiao" @click="changeShp">核销</span>
                    </div>
                </div>
            </div>
            <div class="price active1">
                <div class="user_price">
                    <div class="user_price_left">
                        <img :src="item.user.avatar_url" alt="">
                        <div>{{item.user.name}}</div>
                    </div>
                    <div class="user_price_right">
                        实付：￥{{item.pay_price}}
                    </div>
                    
                </div>
            </div>
            <div class="order_num">
                <span>订单号：{{item.order_no}}</span>
                <img :src="ImgUrl+'home/phone.png'" @click="telPhone(item.notify_mobile)">
            </div>
        </div>  -->
        <div class='loading' v-if="busy"><img :src="ImgUrl+'home/loading.gif'" alt=""></div>
        <div class="di" v-if="page*10 > total">一一 我是有底线的 一一</div>
    </div>
    <!-- </scroll> -->
    <div v-if="showInput||showConsumption" class="mrsk" @click="colse"></div>
    <arertInput :showInput="showInput" @submit="writeOff"></arertInput>
    <consumption :showConsumption="showConsumption" :verify_code="verify_code" @cance="showConsumption=false" @submit="consumptionTrue"></consumption>
  </div>  
</template>
<script>
import arertInput from './components/arertInput.vue'
import consumption from './components/consumption.vue'
import axios from 'axios'
import { getOrderList } from '@/api/order.js'
import { telPhone, goBack, getTokus, getPhone } from '@/utils/native.js'
export default {
  data() {
    return {
      stateNum: 1, // tab页状态
      moreShow:false, 
      activeIndex: -1, // 显示更多的订单索引
      showInput:false, // 核销弹框显示状态
      showConsumption:false, // 确认消费弹框显示状态
      page_size:10, // 分页大小
      page:1, // 当前页数
      verify_status:1, // 核销状态 状态 1-未核销, 2-已核销 3-已退款
      status:[], // 	订单状态 状态 1-未支付 2-已取消 3-已关闭 (超时自动关闭) 4-已支付 5-退款中[保留状态] 6-已退款 7-已完成 (不可退款) 8-待发货 9-待自提 10-已发货
      type:'',  // 订单类型 1-团购订单 2-扫码支付订单 3-点菜订单 4-超市订单
      list:[], // 订单列表  
      total:0,
      busy: false, // 
      busy1:true,
      loadShow: true,
      touchstart:0,
      verify_code:''
    }  
  },
  components:{
    arertInput,
    consumption,
    scroll
  },
  created() {
    getTokus(this)
    setTimeout(() =>{
        this.getOrderList()
    },500)
    window.addEventListener("scroll", this.scrollFunc); // body滚动
    let that = this
  },
  mounted() {
  },
  methods: {
    scrollFunc(e) { // 计算滑动是否到底函数
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      let windowHeight = document.documentElement.clientHeight || document.body.clientHeight;
      let scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight;
      if (scrollTop + windowHeight >= scrollHeight) {
        if(!this.busy&&this.page*10 < this.total){
            this.loadMore()
        }
      }
      if(!this.busy1&&scrollTop<=0){
        this.page=1;
        this.busy1 = true
        this.getOrderList();
      }
    },
    verification() {

    },
    loadMore() {
      this.busy = true;
      this.page++;
      this.getOrderList();        
    },
      // 列表
    getOrderList() {
      getOrderList({
        page_size:this.page_size, 
        page:this.page,
        verify_status:this.verify_status,
        status:this.status,
        type:this.type 
      }).then(data=>{
          
        // if(data.data.total<this.page){
        //   this.page = data.data.total
        // } else{
          if(this.page == 1) {
            this.list = data.data.list   
            this.total = data.data.total
          } else {
            this.list = this.list.concat(data.data.list)   
            this.total = data.data.total
          }
        // }
        setTimeout(()=>{
            this.busy = false
          this.busy1 = false
        },500)
        
        
      })
    },
    // 关闭弹框
    
    colse() {
       this.showConsumption = false 
       this.showInput = false
    },  
    // 切换tab页  
    changeState(num) {
      if(num == 1) {
        this.verify_status = 1
        this.status = ''
      } else {
        this.verify_status = ""
        this.status = [6,7]
      }
      this.page = 1
      this.getOrderList()
    },
    // 显示更多
    changeMoreShow(num) {
      this.activeIndex = num  
    },
    // 点击弹出核销框
    changeShp() {
      this.showInput = true
    },
    // 核销
    writeOff(data) {
      this.showInput = false  
      this.verify_code = data.verify_code
      this.showConsumption = true 
      
    },
    // 消费确认
    consumptionTrue() {
      this.showConsumption = false   
        this.page = 1
      this.getOrderList()
    },
    // 调原生打电话
    telPhone(phone) {
      console.log(phone)
      telPhone(phone)    
    },
    // 调原生返回上一页
    goback() {
        goBack()
    }
  },
}
</script>
<style lang="less" scoped>
#order{
    background-color:#fafafa;
    // height: 13.34rem;
    // overflow: hidden;
    // -webkit-overflow-scrolling:touch;
    // .mint-header-title{
    //     color:#000;
    // }
    .di{
    text-align: center;
    height: .8rem;
    line-height: .8rem;
  }
    .loading{
        width: 100%;
        height: .4rem;
        img{
            display: block;
            width: .4rem;
            height: .4rem;
            margin: 0 auto;
        }
    }
    .mrsk{
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        background-color: rgba(0,0,0,.5)
    }
    .content{
        padding:.2rem .3rem;
        min-height: 12rem;
        .item_order{
            margin-top:.2rem;
            width:100%;
            height:auto;
            background-color: #ffffff;
            box-shadow: 0px .02rem .16rem 0rem 
                rgba(204, 204, 204, 0.6);
            border-radius: .1rem;
            padding:.3rem .2rem 0;
            .price{
                padding-bottom:.3rem;
                border-bottom:.02rem solid #e5e5e5;
            }
            .price.active1{
                padding-bottom:0;
            }
            .order_num{
                width:100%;
                height:1rem;
                display:flex;
                justify-content:space-between;
                align-items:center;
                // border-top:.02rem solid #e5e5e5;
                span:nth-of-type(1){
                    color:#666666;
                    font-family: MicrosoftYaHei;
	                font-size: .26rem;
                }
                img{
                    width:.4rem;
                    height:.4rem;
                }
            }
            .see_more{
                width:100%;
                height:.7rem;
                display:flex;
                align-items:flex-end;
                // justify-content:space-between;
                .more{
                    width: 2rem;
                    height: .5rem;
                    border-radius: .26rem;
                    border: solid .02rem #e5e5e5;
                    margin: 0 auto;
                    line-height:.45rem;
                    text-align:center;
                    display:flex;
                    align-items:center;
                    justify-content:center;
                    span{
                        font-family: MicrosoftYaHei;
                        font-size: .26rem;
                        font-weight: normal;
                        font-stretch: normal;
                        letter-spacing: 0px;
                        color: #999999;
                    }
                    img{
                        margin-left:.1rem;
                        width: .2rem;
                        height: .2rem;
                        transform:rotate(0);
                        transition:all .5s
                    }
                    img.active{
                        transform:rotate(180deg);
                        transition:all .5s
                    }
                }
            }
            .meal_order{
                width:100%;
                >li{
                    width:100%;
                    height:auto;
                    .meal_name{
                        width:100%;
                        display:flex;
                        justify-content:space-between;
                        align-items:center;
                        font-family: MicrosoftYaHei;
                        font-size: .28rem;
                        font-weight: normal;
                        font-stretch: normal;
                        letter-spacing: 0px;
                        color: #333333;
                        span:nth-of-type(2){
                            font-size: .28rem;
                        }
                    }
                    
                }
            }
            .product_order{
                        padding-left:.2rem;
                        >li{
                            width:100%;
                            margin-top:.1rem;
                            display:flex;
                            justify-content:space-between;
                            align-items:center;
                            font-family: MicrosoftYaHei;
                            font-size: .26rem;
                            font-weight: normal;
                            font-stretch: normal;
                            letter-spacing: 0px;
                            color: #999999;
                        }
                        li:nth-of-type(1){
                            margin-top: 0;
                        }
                    }

            .user_price{
                width:100%;
                display:flex;
                justify-content: space-between;
                height:1.36rem;
                align-items: center;
                .user_price_right{
                    display:flex;
                    align-items: center;   
                    font-family: MicrosoftYaHei;
                    font-size: .32rem;
                    font-weight: normal;
                    font-stretch: normal;
                    letter-spacing: 0px;
                    color: #000000; 
                }
                .user_price_left{
                   display:flex;
                   align-items: center;
                   img{
                       width: .72rem;
                        height: .72rem;
                        background-color: #e5e5e5;
                        border-radius: .1rem;
                   }
                   div{
                       margin-left:.2rem;
                       font-family: MicrosoftYaHei;
                        font-size: .3rem;
                        font-weight: normal;
                        font-stretch: normal;
                        letter-spacing: 0px;
                        color: #000000;
                   }
                }
            }
            .hander_order{
                height:.72rem;
                width:100%;
                border-bottom:.02rem solid #e5e5e5;
                .hander_top{
                    width:100%;
                    display:flex;
                    justify-content: space-between;
                    height:.4rem;
                    .order_state{
                        height:.4rem;
                        line-height:.4rem;
                        color: #999999;
                        .hexiao{
                            width: 1rem;
                            height: .5rem;
                            line-height: .48rem;
                            border-radius: .26rem;
                            border:.02rem solid #4a57ff;
                            text-align:center;
                            display:block;
                            color:#4a57ff;
                        }
                    }
                    .hander_time{
                        display:flex;
                        .tuan{
                            width: .4rem;
                            height: .4rem;;
                            background-color: #7ec157;
                            border-radius: .04rem;
                            text-align:center;
                            line-height:.4rem;
                            color:#fff;
                        }
                        .dan{
                            width: .4rem;
                            height: .4rem;;
                            background-color: #16c4e3;
                            border-radius: .04rem;
                            text-align:center;
                            line-height:.4rem;
                            color:#fff;
                        }
                        .mai{
                            width: .4rem;
                            height: .4rem;;
                            background-color: #ed125a;
                            border-radius: .04rem;
                            text-align:center;
                            line-height:.4rem;
                            color:#fff;
                        }
                        .time{
                            margin-left:.12rem;
                            font-size: .26rem;
                            font-weight: normal;
                            font-stretch: normal;
                            letter-spacing: 0px;
                            color: #666666;
                            line-height:.4rem;
                        }
                    }
                }
                
            }
        }
        .item_order:nth-of-type(1){
            margin-top:0;
        }
    }
    .table_nav{
        width:100%;
        height:.9rem;
        display:flex;
        border-bottom:.02rem solid #e5e5e5;
        div{
            flex:1;
            line-height:.9rem;
            font-family: MicrosoftYaHei;
            font-size: .32rem;
            font-weight: normal;
            font-stretch: normal;
            letter-spacing: 0px;
            color: #333333;
            border-bottom:.04rem solid #fff;
            text-align:center;
        }
        div.active{
            color: #4a57ff;
            border-bottom:.04rem solid #4a57ff;
        }
    }
}   
</style>


