<template>
  <div class="arertInput" :class="showInput?'active':''">
    <input type="text" placeholder="请输入核销码" v-model="verify_code">  
    <div class="text">仅支持一次核销订单全部消费码</div>  
    <mt-button @click="submit" style="width:100%;background-color: #4a57ff;margin-top: .9rem;" type="primary">核销</mt-button>
  </div>
</template>
<script>

export default {
  props:{
    showInput:{
      default:false
    }
  },
  data() {
    return {
      verify_code:'', // 核销码
    }
  },
  methods: {
    submit() {
      // verification({
      //   verify_code:this.verify_code
      // }).then(data => {
      //   if(data.code === 0) {
          
      //   }
      // })
      let data = {
        num : 1,
        verify_code:this.verify_code
      }
      this.$emit('submit',data)
    }
  }  
}
</script>
<style lang="less" scoped>
  .arertInput{
    width: 6rem;
    height: 4.24rem;
    position: fixed;
    background-color: #fff;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%) scale(0);
    overflow: hidden;
    border-radius: .1rem;
    transition: all .5s;
    padding: .6rem .5rem .4rem;
    input{
      height: 1rem;
      background-color: #ffffff;
      border-radius: .1rem;
      border: solid .02rem #e5e5e5;
      text-align: center;
      font-family: MicrosoftYaHei;
      font-size: .36rem;
      font-weight: normal;
      font-stretch: normal;
      letter-spacing: 0px;
      color: #cccccc;
    }
    .text{
      text-align: center;
      font-family: MicrosoftYaHei;
      font-size: .26rem;
      font-weight: normal;
      font-stretch: normal;
      line-height: .44rem;
      letter-spacing: 0px;
      color: #999999;
      margin-top: .16rem;
      
    }
  }
  .arertInput.active{
    transform: translate(-50%,-50%) scale(1);
    transition: all .5s;
  }
</style>



